<template>
  <div class="follow-box">
    <div class="follow-bar">
      <van-nav-bar
        :title="$t('datSource5')"
        left-arrow
        @click-left="linkHome"
      />
    </div>
    <div class="content">
      <div class="top">
        <div >{{$t('FollowT')}}</div>
        <div>
          <div>{{$t('account')}}：{{'***'+(account).substring(account.length-3)}}</div>
          <div style="text-align: right">{{$t('win')}}：<span :style="{color:this.$route.params.data.winRate?'#FD4F50':'#07B76F' }">{{RateShow}}</span></div>
        </div>
      </div>
      <div class="upBankCard-input">
        <van-cell-group>
          <van-cell :title="$t('FollowD')" class="direction">
            <div slot="default">
              <van-switch  v-model="checked" inactive-color="#fff" class="switch" active-color="#fff"/>
              <div class="text" @click="clickSwitch1" :style="{color:checked === true?'#fff':'#666666'}"> {{$t('back')}} </div>
              <div class="text text1" @click="clickSwitch"  :style="{color:checked === false?'#fff':'#666666'}">{{$t('positive')}}</div>
            </div>
          </van-cell>
          <van-field v-model="followValue" ref="followValue" type="number" :label="$t('PAOP')" :placeholder="$t('PIBS')" input-align="right">
            <div class="nameClass" slot="right-icon" >{{$t('PIB')}}</div>
          </van-field>
        </van-cell-group>
      </div>
      <div class="bottom"><span style="color: #EF3F38">{{$t('example')}}：</span>{{$t('example1')}}</div>
    </div>
    <div class="but" @click="setFollow">{{$t('ConfirmOrder')}}</div>
  </div>
</template>

<script>
  import { Toast } from 'vant';
  export default {
    name: "follow",
    data(){
      return{
        checked:false,
        account:'******',
        RateShow:'0%',
        followValue:'',
        direction:0,
      }
    },
    created(){
      let that = this
      this.uid = localStorage.getItem('uid');
      this.discUrl = localStorage.getItem('discUrl')
      let routeData = this.$route.params.data
      this.account = routeData.account
      if(routeData.winRate){
        this.RateShow = routeData.winRateShow
      }else {
        this.RateShow = routeData.lostRateShow
      }
      this.$nextTick( () => {
        that.$refs.followValue.focus()
      })
    },
    methods:{
      linkHome(){
        this.$router.push({
          name:'ranking'
        })
      },
      clickSwitch(){
        this.checked = false
        this.direction = 0
        console.log(this.checked,this.direction,'正向')
      },
      clickSwitch1(){
        this.checked = true
        this.direction = 1
        console.log(this.checked,this.direction,'反向')
      },
      changeSwitch(){
        console.log(this.checked)
      },
      // 提交跟单
      setFollow(){
        let that = this
        if(this.followValue !== ''){
          let postData = {}
          let data = {}
          data.userId = this.uid
          data.isFollowedId = this.$route.params.data.userId
          data.multiple = this.followValue
          data.direction = this.direction
          postData.documentary = data
          postData.type = 1
          this.$axios.post('http://'+this.discUrl+':7777/api/documentary/insertDocumentary',postData).then(res =>{
            console.log(res)
            if(res.data.code === 0){
              Toast({
                duration:2000,
                position:'bottom',
                message:that.$t('documentaryS'),
                onOpened() {
                  that.$router.push({
                    name:'ranking'
                  })
                }
              })
            }else {
              Toast({
                duration:2000,
                position:'bottom',
                message:res.data.msg,
              })
            }
          })
        }else {
          Toast({
            duration:2000,
            message:that.$t('NBS'),
            position:'bottom'
          })
        }

      }
    }
  }
</script>

<style scoped lang="scss">
  @import "../../style/home";
</style>



